.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.About-me{
  background-color: #282c34;
  color: white;
  min-height: 10vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-link{
  color: bisque;
}

.Logo{
  background-image: url(./KH2.png);
  margin-top: 10px;
}

#logo-header{
  margin-right: 10px;
  
}

#logoss{
  font-weight: 800;
  
  background-color: #3f2a14;
}

