.Me{
    background-image: url(./meee2copy.jpg);
    
}

#yo-soy{
    margin-top: 80px;
}

.my-face {
    display: flex;
    flex-flow: column;
    align-items: center;
    
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    margin: auto;
    text-decoration: none;
}

.about-me{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: flex;
    flex-flow: column;
    align-items: center;
    
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    margin: auto;
    text-decoration: none;
    margin-top: 30px;
}

.my-name{
    font-size: 60px;
    font-weight: 500px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.more-me{
    font-size: 20px;
    margin-left: 200px;
    margin-right: 200px;
    margin-bottom: 110px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.social-container{
    padding: 25px 50px;
}

a.social{
    margin: 0 1rem;
    transition: transform 250ms;
    display: incline-block;
}

a.social:hover{
    transform: translateY(-2px);
    transition-duration: 0.3s;
}

.allIcons:hover{
    transform: translateY(-5px);
    transition-duration: 0.3s;
}








